.container {
    min-height: 100vh;
    background: #f8f5f2;
    display: grid;
    margin: 0;
    grid: 50%/50% 50%;
}

.right_side {
    height: 100vh;
    padding-left: 2vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 20%;
    align-items: flex-start;
}

.A_healine {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.A_healine p {
    font-size: 1rem;
}

.line {
    width: 7%;
    margin: 0 1vw 0 0;
    height: 3px;
    background-color: var(--bg);
}

.head {
    margin: 0%;
    padding: 0%;
    font-size: 2rem;
    font-weight: 600;
}

.body_text,
.body_text_mv {
    font-size: 1rem;
    color: hsl(0, 0%, 53%);
    font-weight: 400;
}

.body_text_mv {
    display: none;
}

.button {
    height: 6vh;
    width: 12vw;
    background: none;
    border: 2px solid var(--bg);
    border-radius: 5px;
    color: var(--bg);
    font-size: 0.8rem;
    cursor: pointer;
}

.left_side {
    height: 100vh;
    display: grid;
    place-items: center;
}

.ilustration {
    height: 65%;
}

@media screen and (max-width: 480px) {
    .ilustration {
        height: 50%;
    }
    .left_side {
        height: auto;
        order: 2;
    }
    .right_side {
        height: auto;
        order: 1;
    }
    .container {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .body_text {
        display: none;
    }
    .body_text_mv {
        display: block;
    }
    .button {
        position: absolute;
        height: 6vh;
        width: 50vw;
        bottom: 0%;
        font-size: 0.8rem;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}