body {
    overflow-x: hidden;
}

.App {
    min-height: 100vh;
    background-color: var(--bg);
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.menu {
    width: 100%;
    position: fixed;
}

.icon {
    position: fixed;
    top: 3%;
    right: -5%;
    height: 5vh;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.menu_open,
.menu_close {
    height: 5vh;
    display: none;
}

@media screen and (max-width: 480px) {
    .menu {
        position: fixed;
        width: 80vw;
        right: 0%;
        z-index: 1;
        background-color: #313131;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    .menu_open {
        display: block;
    }
    .menu_close {
        display: none;
    }
    .icon.active .menu {
        display: flex;
    }
    .icon.active .menu_open {
        display: none;
    }
    .icon.active .menu_close {
        display: block;
    }
}