:root {
    --size: 10vh;
}

.menu {
    height: var(--size);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow: hidden;
    color: #fff;
}

.main {
    width: 100%;
    height: var(--size);
    display: flex;
    justify-content: center;
    align-items: center;
}

.ul,
.IconUl {
    list-style: none;
    display: flex;
    flex-direction: row;
}

.li {
    margin: 0 1vw;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.main .li {
    position: relative;
}

.iconify {
    color: #fff;
    font-size: 2rem;
}

.line {
    position: absolute;
    left: 0%;
    top: 50%;
    width: 0%;
    height: 3px;
    background: #fff;
    transition: 500ms ease;
}

.li:hover .line {
    width: 80%;
}

@media screen and (max-width: 480px) {
    .menu {
        height: 100vh;
        display: flex;
        right: -100%;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        transition: 1s ease;
    }
    .menu.active {
        right: 0%;
    }
    .last .IconUl {
        flex-direction: row;
    }
    .last {
        position: absolute;
        bottom: 20%;
    }
    .ul {
        flex-direction: column;
    }
    .li {
        font-size: 2rem;
        justify-content: flex-start;
        margin: 5% 25% 0 5%;
    }
    .li:hover .line {
        width: 0%;
    }
}